module.exports = function(props) {
    // private
    const that = this;
    // data = { string: "" }

    function generateOption(data) {
        return `
<div>
 <button onclick="ui.selectOption({ element_id: '${data.element_id}', option_id: '${data.option_id}' })">${data.label}</button>
</div>
`;
    }

    function findOptionById(data) {
        for(var i = 0; i < that.main_state.options.length; ++i) {
            if(data.option_id === that.main_state.options[i]._id) {
                return that.main_state.options[i];
            }
        }
    }

    // public
    this.main_state = { options: [], displaying: 0, update: () => {} }
    this.props = props;

    // privileged

    this.displayElement = function() {
        this.main_state.displaying = 0;
        props.display({ element_id: props._id, element: this });
    }

    this.displayOptions = function() {
        this.main_state.displaying = 1;
        props.display({ element_id: props._id, element: this });
    }

    this.selectOption = async function(data) {
        await findOptionById(data).method(props);
        this.displayElement();
    }

    this.display = function() {
        if(this.main_state.displaying === 0) {
            return this.generateElement();
        }
        else if(this.main_state.displaying === 1) {
            return `
            ${this.main_state.options.map((d) => {
                return d.element;
            }).join(" ")}
            `
        }
        else {
            return this.generateForm();
        }
    }

    this.updateData = function(data) {
        this.main_state.update(this.getFormInput(data));
        this.displayElement();
    }

    this.option = function(data, method) {
        data.element_id = props._id;
        data.option_id = props.method.uuid();
        this.main_state.options.push({ _id: data.option_id, element: generateOption(data), method: method });
    }

    this.form = function(data) {
        return new Promise((resolve, reject) => {
            this.main_state.displaying = 2;
            props.display({ element_id: props._id, element: this });
            this.main_state.update = resolve;
        });
    }

    this.update = function(data) {
        this.updateProps(data);
        props.display({ element_id: props._id, element: this });
    }

    this.remove = function() {
        setTimeout(() => { props.remove({ element_id: props._id }); }, 5);
    }

    this.style = function(data) {
        props.style = data;
    }

    function init() {
        props._id = props.method.uuid();
    }

    init();

  };