const Text = require("./text.element");
const Image = require("./image.element");
const Video = require("./video.element");
const Form = require("./form.element");
const Camera = require("./camera.element");
const Selection = require("./selection.element");

const { v4: uuid } = require("uuid");
const Main = require("./main.element");
const markdown = require("markdown" ).markdown;
const dompurify  = require("dompurify");

const method = { uuid: uuid, Main: Main, markdown: markdown, dompurify: dompurify };

module.exports = {
  text: (data) => { return new Text({ ...data, method: method }) },
  image: (data) => { return new Image({ ...data, method: method }) },
  video: (data) => { return new Video({ ...data, method: method }) },
  form: (data) => { return new Form({ ...data, method: method }) },
  camera: (data) => { return new Camera({ ...data, method: method }) },
  selection: (data) => { return new Selection({ ...data, method: method }) }
};
