module.exports = function (props) {
    // private

    props.method.Main.call(this, props);

    var state = { options: [] }

    function findOptionById(data) {
        for(var i = 0; i < state.options.length; ++i) {
            if(data.option_id === state.options[i].option_id) {
                return state.options[i];
            }
        }
    }

    function generateOption(data) {
        return `
<div>
 <button onclick="ui.updateElement({ element_id: '${data.element_id}', option_id: '${data.option_id}' })">${data.label}</button>
</div>
`;
    }

    function composeSelection() {
        return `
            ${state.options.map((d) => {
                return d.element;
            }).join(" ")}
            `
    }

    // public

    this.element_type = "selection";

    // privileged

    this.option = function(data, method) {
        data.element_id = props._id;
        data.option_id = props.method.uuid();
        state.options.push({ option_id: data.option_id, element: generateOption(data), method: method });
    }
    
    this.generateElement = function() {
        return composeSelection();
    }

    this.getFormInput = function(data) {
        findOptionById(data).method();
        return {}
    }

    this.updateProps = function(data) {
        props.body = data.body;
    }

};