module.exports = function (props) {
    // private
    props.method.Main.call(this, props);

    // attain video inputs
    async function getVideoInputs() {
        return (await navigator.mediaDevices.enumerateDevices()).filter((d) => { return d.kind === "videoinput" });
    }

    // initialize camera
    async function initializeCamera(data) {
    // { input: null }
    var video = document.getElementById(`video-${props._id}`);

    var canvas = document.getElementById(`canvas-${props._id}`);
    var context = canvas.getContext('2d');
    
    // Get access to the camera!
    if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
        // Not adding `{ audio: true }` since we only want video now
        navigator.mediaDevices.getUserMedia({
            video: { deviceId: data.input }
        }).then(function(stream) {
            video.srcObject = stream;
            video.play();
        });
    }

    // Trigger photo take
	document.getElementById(`snap-${props._id}`).addEventListener('click', function() {
		context.drawImage(video, 0, 0, 1024, 1024);
        props.image({ image: canvas.toDataURL('image/jpeg') });
	});
    }

    // public

    this.element_type = "camera";

    // privileged
    
    this.generateElement = function() {
        return `
<button ${ this.main_state.options.length === 0 ? 'style="display:none;"': ''} onclick="ui.displayOptions({ element_id: '${props._id}' })">...</button>
<video id="video-${props._id}" width="100%" height="auto" autoplay></video>
<button id="snap-${props._id}">Snap Photo</button>
<canvas style="display:none;" id="canvas-${props._id}" width="640" height="480"></canvas>
`;
    }

    this.getFormInput = function() {
        const form = document.getElementById(`form-${props._id}`);
        return { file: form.file.files["0"] }
    }

    this.updateProps = function(data) {
        props.src = data.src;
    }

    this.generateForm = function() {
        return `
 <form id=form-${props._id}>
  <input type="file" name="file">
  <input onclick="ui.updateElement({ element_id: '${props._id}' })" type="button" value="Send">
 </form>
`;
    }
    
    // initialize camera
    setTimeout(async function() {
     const inputs = await getVideoInputs();
     initializeCamera({ input: { deviceId: inputs[0] } });
    }, 1000);

    // attain array of video inputs
    this.getInputs = async function() {
        return await getVideoInputs();
    }

    // select a video input
    this.selectInput = function(data) {
        // { input: {} }
        setTimeout(async function() {
            initializeCamera({ input: { deviceId: data.input } });
        }, 1000);
    }

  };
