module.exports = function (props) {
    // private

    props.method.Main.call(this, props);

    var state = { inputs: [], submit: ()=>{} }

    function inputText(data) {
        return `
<input id=form-${data.name}-${props._id} style="width:98%; padding:1%; padding-top:10px; padding-bottom:10px; type="text" name="${data.name}" placeholder="${data.placeholder}">
`;
    }

    function inputHidden(data) {
        return `
<input id=form-${data.name}-${props._id} style="width:98%; padding:1%; padding-top:10px; padding-bottom:10px; type="password" name="${data.name}" placeholder="${data.placeholder}">
`;
    }

    function inputTextarea(data) {
        return `
<textarea id=form-${data.name}-${props._id} name="${data.name}" placeholder="${data.placeholder}"></textarea>
`;
    }

    function inputFile(data) {
        return `
<input id=form-${data.name}-${props._id} type="file" multiple="multiple" name="${data.name}">
`;
    }

    function composeForm() {
        const inputs = state.inputs.map((d) => {
            switch(d.type) {
                case "text": return inputText(d);
                case "password": return inputHidden(d);
                case "textarea": return inputTextarea(d);
                case "file": return inputFile(d);
            }
        }).join("");
        return `
         <form id=form-${props._id}>
         ${inputs}
         <input onclick="ui.updateElement({ element_id: '${props._id}' })" type="button" value="Send">
         </form>
        `;
    }

    // public

    this.element_type = "form";

    // privileged

    this.input = function(data) {
        // if placeholder doesn't exist, use the existing name property
        if(!data.hasOwnProperty("placeholder")) {
            data.placeholder = data.name;
        }
        state.inputs.push(data);
        return { _id: `form-${data.name}-${props._id}` }
    }

    this.submit = function(data) {
        state.submit = data;
    }

    this.reset = function() {
        const form = document.getElementById(`form-${props._id}`);
        form.reset();
    }
    
    this.generateElement = function() {
        return composeForm();
    }

    this.getFormValue = function(data) {
        const form = document.getElementById(`form-${props._id}`);
        switch(data.type) {
            case "text": return form[data.name].value;
            case "password": return form[data.name].value;
            case "textarea": return form[data.name].value;
            case "file": return form[data.name].files;
        }
    }

    this.getFormInput = function() {
        var data = {};
        for(var i = 0; i < state.inputs.length; ++i) {
            data[state.inputs[i].name] = this.getFormValue(state.inputs[i]);
        }
        state.submit(data);
        return {}
    }

    this.updateProps = function(data) {
        props.body = data.body;
    }

};
