module.exports = function(props) {
    // private

    function style(element, className) {
        // setting the div class for containers
        document.getElementById(`element-${element.props._id}`).className += `box ${className}`;
        // setting the unique style of each element
        if(element.props.hasOwnProperty("style")) {
            Object.keys(element.props.style).map((d) => {
                document.getElementById(`element-${element.props._id}`).style[d] = element.props.style[d];
            });
        }
    }

    function text(data) {
        const element = data.elements[data.index];
        if(data.index + 1 === data.elements.length) {
            style(element, "full_width");
            return;
        }
        switch(data.elements[data.index + 1].element_type){
            case "text": style(element, "full_width"); break;
            case "image": style(element, "two_thirds"); break;
            case "video": style(element, "full_width"); break;
            case "form": style(element, "full_width"); break;
            case "camera": style(element, "full_width"); break;
            case "selection": style(element, "full_width"); break;
        }
    }

    function image(data) {
        const element = data.elements[data.index];
        if(data.index + 1 === data.elements.length) {
            style(element, "one_third");
            return;
        }
        switch(data.elements[data.index + 1].element_type){
            case "text": style(element, "one_third"); break;
            case "image": style(element, "one_third"); break;
            case "video": style(element, "one_third"); break;
            case "form": style(element, "one_third"); break;
            case "camera": style(element, "full_width"); break;
            case "selection": style(element, "full_width"); break;
        }
    }

    function video(data) {
        const element = data.elements[data.index];
        if(data.index + 1 === data.elements.length) {
            style(element, "full_width");
            return;
        }
        switch(data.elements[data.index + 1].element_type){
            case "text": style(element, "full_width"); break;
            case "image": style(element, "full_width"); break;
            case "video": style(element, "full_width"); break;
            case "form": style(element, "full_width"); break;
            case "camera": style(element, "full_width"); break;
            case "selection": style(element, "full_width"); break;
        }
    }

    function form(data) {
        const element = data.elements[data.index];
        if(data.index + 1 === data.elements.length) {
            style(element, "full_width");
            return;
        }
        //console.log(data.elements[data.index + 1].element_type);
        switch(data.elements[data.index + 1].element_type){
            case "text": style(element, "full_width"); break;
            case "image": style(element, "two_thirds"); break;
            case "video": style(element, "full_width"); break;
            case "form": style(element, "full_width"); break;
            case "camera": style(element, "full_width"); break;
            case "selection": style(element, "full_width"); break;
        }
    }

    function camera(data) {
        const element = data.elements[data.index];
        if(data.index + 1 === data.elements.length) {
            style(element, "full_width");
            return;
        }
        //console.log(data.elements[data.index + 1].element_type);
        switch(data.elements[data.index + 1].element_type){
            case "text": style(element, "full_width"); break;
            case "image": style(element, "two_thirds"); break;
            case "video": style(element, "full_width"); break;
            case "form": style(element, "full_width"); break;
            case "camera": style(element, "full_width"); break;
            case "selection": style(element, "full_width"); break;
        }
    }

    function selection(data) {
        const element = data.elements[data.index];
        if(data.index + 1 === data.elements.length) {
            style(element, "full_width");
            return;
        }
        //console.log(data.elements[data.index + 1].element_type);
        switch(data.elements[data.index + 1].element_type){
            case "text": style(element, "full_width"); break;
            case "image": style(element, "two_thirds"); break;
            case "video": style(element, "full_width"); break;
            case "form": style(element, "full_width"); break;
            case "camera": style(element, "full_width"); break;
            case "selection": style(element, "full_width"); break;
        }
    }

    // public

    // privileged

    this.setWidth = function(data) {
        switch(data.elements[data.index].element_type){
            case "text": text(data); break;
            case "image": image(data); break;
            case "video": video(data); break;
            case "form": form(data); break;
            case "camera": camera(data); break;
            case "selection": selection(data); break;
        }
    }

  };